.reveal {
  height: 70vh !important; }

.reveal-video-sync {
  position: relative;
  font-size: 12px;
  height: 30vh;
  z-index: 100; }
  .reveal-video-sync video {
    max-width: 100%;
    max-height: 100%; }
  .reveal-video-sync .reveal-video-sync-player {
    position: absolute;
    left: 0;
    right: auto;
    bottom: 0;
    width: 46.22222vh;
    height: 26vh;
    margin: 2vh; }

.reveal.reveal-video-sync-right .controls {
  left: 10px;
  right: auto; }

.reveal.reveal-video-sync-right ~ .reveal-video-sync .reveal-video-sync-player {
  left: auto;
  right: 0; }
